
import React, {FunctionComponent} from 'react';

import {
  GoogleMap as _GoogleMap,
  useLoadScript,
} from '@react-google-maps/api';

import { ComponentProps } from '../ComponentProps.d';

const _log = require('debug')('app:map');

const INIT_MAP = {
  center: {
    lat: 36.5,
    lng: 128,
  },
  zoom: 7,
}

const onClick = (e: any) => {
  const { pixel, latLng } = e;
  //console.log('onClick args: ', e)
  _log('Lat: ', latLng.lat(), ', Lng: ', latLng.lng(), ', Pixel: ', pixel);
}

const googleMapsLibraries = ['drawing', 'visualization', 'places']

const loaderId = 'loader-GooGleMap';
const googleMapId = 'instance-GooGleMap';

interface GoogleMapProps extends ComponentProps {
  googleMapsApiKey: string;
  language: string;
  onClickMap: ((e: any) => void) | null;
  mapContainerStyle: any;
}

const GoogleMap: FunctionComponent<GoogleMapProps> = ({
  t,
  googleMapsApiKey, 
  language,
  onClickMap,
  mapContainerStyle,
  children
}) => {

  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey,
    id:loaderId,
    language,
    region:'KR',
    version: 'weekly',
    libraries: googleMapsLibraries,
    //preventGoogleFontsLoading: true,
  });

  const onLoad = React.useCallback(
    function onLoad (mapInstance) {
      _log('script loaded...');
      // do something with map Instance
    }
  , []);

  if (loadError) return <div>{t('map.load-failure')}</div>

  if (!isLoaded) return <div>{t('map.loading')}</div>

  return (
    <div className='map'>
      <div className='map-container'>
        <_GoogleMap
          id={googleMapId}
          mapContainerStyle={mapContainerStyle}
          zoom={INIT_MAP.zoom}
          center={INIT_MAP.center}
          onClick={onClickMap || onClick}
          onLoad={onLoad}
        >
          {children}
        </_GoogleMap>
      </div>
    </div>
  );
}

export { GoogleMap };
